import { checkMobile } from '../../common/utils.js'
export default {
	components: {},
	data() {
		return {
			mobile: '',
			code: '',
			nowCode:'',
			password: '',
			codeBool: false,
			timer: null,
			seconds: 60,
			state: false
		}
	},
	methods: {
		//验证码倒计时
		countDown() {
			this.state = true
			this.timer = setInterval(() => {
				this.seconds--
				if (this.seconds <= 0) {
					clearInterval(this.timer)
					this.state = false
					this.seconds = 60
					return
				}
			}, 1000)
		},
		//获取验证码
		getCode() {
			if (checkMobile(this.mobile)) {
				if (!this.state) {
					this.countDown()
					this.$http({
						api: 'user/sendcode',
						method: 'POST',
						params: {
							mobile: this.mobile
						}
					}).then(res => {
						let getNum = res.data.message.replace(/[^0-9]/ig,"")
						this.nowCode = Number(getNum)
					}).catch(err => {

					})
				}
			} else {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的手机号'
				})
			}
		},
		//验证
		checked(){
			return new Promise((resolve,reject)=>{
				let tip = ''
				if(!checkMobile(this.mobile)){
					tip = '请输入正确的手机号'
					reject()
				}else if(this.code != this.nowCode){
					tip = '请输入正确的验证码'
					reject()
				}else if(!this.password){
					tip = '请输入密码'
					reject()
				}else{
					resolve()
				}
				if(tip){
					uni.showToast({
						icon:'none',
						title:tip
					})
				}
			})
		},
		//确认
		confirm() {
			this.checked().then(()=>{
				this.$http({
				 	api: 'user/forgetpass',
				 	method: 'POST',
				 	params: {
				 		mobile: Number(this.mobile),
				 		code: Number(this.code),
				 		password: this.password
				 	}
				 }).then(res => {
				 	uni.showToast({
				 		title: '重置成功',
				 		success: res => {
				 			setTimeout(() => {
				 				uni.navigateBack()
				 			}, 1000)
				 		}
				 	})
				 }).catch(err => {
				 
				 })
			}).catch((err)=>{})
		}
	}
}
